<template>
	<div class="wrapper">
	<form>
		<mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
		<mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
	</form>

		<mt-button type="danger" size="normal" @click="login">登录</mt-button>
	</div>
</template>
<script>
import {mapActions} from "vuex";
	export default {
		data (){
			return {
				key:this.$route.params.key,
				username:'',
				password:'',

			}
		},
		created(){
		},
		methods:{
			...mapActions([
				'setUser',
				'setInfo'
			]),
			login(){
				this.axiosLogin();
			},
			axiosLogin(){
				this.$axios.get(`https://www.apiopen.top/login?key=${this.key}&phone=${this.username}&passwd=${this.password}`)
				.then(res=>{
					if(res.status == 200){
						this.setInfo(res.data.data);
						this.$Cookies.set("login",1);
						this.setUser(true);
						this.$router.push({
							path:"/",
						})
					}
					
				})
				.catch(err =>{
					console.log(err);
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.wrapper{
		padding: 0 12px;
		.mint-button{
			width:100%;
			margin-top:10px;	
		}
	}
</style>


